*{
  margin:0px;
  padding:0px;
  box-sizing: border-box;
}
.App{
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  min-height: 100vh;
}
.form{
  text-align: center;
  padding:40px 0px;
  margin-bottom: 50px;
}
input{
  width: 35vw;
  height: 40px;
  padding:0px 20px;
  font-size:20px;
  border:none;
  border-radius: 10px;
}
button{
  padding:10px 30px;
  margin-left: 50px;
  background-color: #fd9c0b;
  color:#fff;
  border:none;
  border-radius: 10px;
  cursor: pointer;
}
input,button:focus{
  outline: none;
}
.main{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}